import Vue from 'vue'
import App from './mainpage.vue'

//在这里引入 bootstrap。默认只引入 bootstrap 中的 js，css 需要另外引入，我的 bootstrap.ss 在APP.vue中引入的
//import "bootstrap";
//也可以在这里引入 bootstrap.css ;
//import "bootstrap/dist/css/bootstrap.css";

Vue.config.productionTip = false;

//全局过滤器，需写在new Vue之前，不可写在其他.vue文件中，因为 Vue 在局部 undefind
Vue.filter('filter',function(msg){
  return msg;
});

import axios from 'axios'
//需要在原型定义 axios 否则其他组件无法使用
Vue.prototype.axios = axios;
// 配置有效性优先顺序：请求 config 参数 >>> 实例 defaults 属性 >>> lib/defaults.js 库默认值
